.routes {
  position: absolute;
  top: 100vh;
  width: 100%;
}

.route {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.text {
  color: white;
  margin: 20px 160px;
}

.title {
  font-size: 42px;
}

.detail {
  font-size: 24px;
}

.about.route {
  background-color: rgb(0, 183, 255);
}

.code.route {
  background-color: rgb(255, 136, 0);
}

.music.route {
  background-color: rgb(94, 255, 0);
}

.contact.route {
  background-color: rgb(204, 0, 255);
}



